*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Montserrat', sans-serif;
  background-color: #464646;
  
}


.Add_cart_button{
  margin-top: 20px;
  
  width: 100%;
  cursor: pointer;
  
  
}
.inputbox{
  border-top-style: hidden;
  text-decoration: none;
}

.Add_cart_button button{
  display: block;
  margin: 0 auto;
  border-width: 1.5px;
  background-color:#001e28;
  transition: all .5s;
 
  
  color: #ffffffff;
  box-shadow: 3px 3px 6px #001e28;
  border-radius: 4px;
  
  border-color: #001e28;
  
}
.Add_cart_button button:hover{
  background-color: #fffefe;
  color: black;
  border-color: #001e28;
  box-shadow: 3px 3px 6px #0a0a0a; 
  
}
.ant-layout-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}
#cart-items {
  position: relative;
  margin-right: 25px;
}


#cart-items span{
  font-size: 18px;
}
.ant-layout-header .anticon svg 
{font-size: 30px;
  vertical-align: middle;
}
#cart-badget { 
  margin-right: 25px;
  padding: 5px;
  background-color: #001e28;
  color: #fffefe;
  border-radius: 11px 11px 11px 11px;
  cursor: pointer;
 
  
}

.del span{
  
  color: black;
  font-size: 25px;
  background-color: #ffffffff;
  border-radius: 11px 11px 11px 11px;
  width: 27px;
  height: 38px;
  padding-top: 4px;


}
.del span:hover{
  
    color: #B66878;
    
    font-size: 30px;
    background-color: #b1d295;
    border-radius: 11px 11px 11px 11px;
    width: 29px;
    height: 38px;
    padding-top: 4px;
    box-shadow: 0px 3px 5px 1px;
    
}
.del{
  color: black;
  font-size: 21px;
  /* background-color: #b1d295; */
  display:flex;
  border-radius: 11px 11px 11px 11px;
  width: 27px;
  cursor: pointer;
  text-align: center;
 
}
.del:hover{
  
  font-size: 21px;
  border-radius: 11px 11px 11px 11px;
  border-radius: 11px 11px 11px 11px;
  /* background-color:#b1d295; */
  /* color: burlywood; */
  /* box-shadow: 0px 2px 9px -2px; */
  width: 29px;
}
.plus{
  margin-right: 5px;
}
.minus{
  margin-left: 5px;
}
.categoryFlex{
  display: flex;
  flex-direction: column;
  margin:  0 30px;
  cursor: pointer;
  
  align-items: center;
  justify-content: center;
  /* height: 100px;
  width: 70px; */
  
  
}
.categoryFlex:active{
  display: flex;
  flex-direction: column;
  margin:  0 30px;
  cursor: pointer;

  align-items: center;
  justify-content: center;
  
    
}
.category{display: flex;
  justify-content: center;
  margin-bottom: 50px;
  padding: 5px; 
  
}
.category-active{
  box-shadow: 0 0 9px 2px #ac5465;
  border-radius:  9px 9px 6px 6px;
  padding-bottom: 4px;
    padding-left: 4px;
    padding-right: 4px;
